import { useEffect, useState } from "react"
import C from "./krectuk_noluk.module.css"
import Game from "./Game/Game"
import localforage from "localforage";

const input_p = {
    
first: {
    value : '',
    style: null
},
second : {
    value : '',
    style: null
}
}



function Krectuk_noluk(){

    
    const history = localforage.createInstance({
        name: "history"
    });
    
    const [inp_and_butt,setInp_and_butt] = useState(true)
    const [inp, setInp] = useState(input_p)
    
    
//history.clear()
    
    
    const new_restart=()=>{
        setInp(input_p)
setInp_and_butt(true)
history.clear()
// setGlobalInf()

}

        const start_game=()=>{
            if(inp.first.value.trim()&&inp.second.value.trim()){
                setInp_and_butt(false)
                
            } else { 
                
                setInp(
                    {...inp,
                        first:{...inp.first,
                            style:`1px solid red`},
                            second:{...inp.second,
                                style:`1px solid red`}
                            }
                            )
                        }
                        
                    }
                    
                    
                    
                    useEffect(()=>{
                        history.getItem('history')
                    .then(his=>{
                        if(!his) return
                            setInp_and_butt(false)
                    })
                    },[])
   
    return(
        
        <div className={C.main_game}>
          {inp_and_butt   
       ? <div className={C.input_button}>
            <input onInput={e=>setInp({
                    ...inp, first: {...inp.first, value : e.target.value}
                })} 
                value={inp.first.value}
                style={{border :inp.first.style}}
                type="text"/>
            <input onInput={e=>setInp({...inp, second:{...inp.second, value:e.target.value}
            })}  
            value={inp.second.value}
            style={{border :inp.second.style}}
            type="text"/>
             <button onClick={start_game} >Start</button>   
         </div>
         :<Game first={inp.first.value} history={history} rest={new_restart} second={inp.second.value}/> 

     }


        </div>
       

    )
    
}
export default Krectuk_noluk